
body {
max-width: 1920px
}

* {
margin: 0;
padding: 0;
box-sizing: border-box;
}


.logo__toxin {
	background-image: url("\logo_toxin.svg");
	width: 48px;
	height: 48px;
}

.logo__toxin_margin_30 {
	margin: 30px;
}

.color-and-types__container {
display: flex;
flex-direction: row;
justify-content: space-between;
}

.color-and-types__colors {
display: flex;
flex-direction: column;
box-sizing: border-box;	
margin-left: 150px;
}

.color-block{
display: flex;
margin-bottom: 20px;
}

.color-block__rectangle {
height: 70px;
min-width: 70px;
border-radius: 6px;
}

.color-block__text {
display: flex;
flex-direction: column;
justify-content: space-around;
padding-left: 30px;
min-width: 189px;
}

.color-block__text_bold {
font-family: Quicksand;
font-weight: 700;
font-size: 19px;
}

.color-block__text_normal {
font-family: Quicksand;
font-weight: 400;
font-size: 19px;
}

.color-and-types__types {
display: flex;
flex-direction: column;
justify-content: flex-start;
min-width: 403px;
margin-right: 191px;
margin-left: 150px;
}

.type-block {
display: flex;
margin-bottom: 50px;	
max-width: 400px;
}

.type-block__text {
padding-left: 10px;
min-width: 300px;
}

.type-block__head  {
font-family: Montserrat;
font-style: normal;
font-weight: 400;
text-align: right;
min-width: 60px;
padding-right: 10px;
}

.type__heading_color_light-shade5 {
color: rgba(31, 32, 65, 0.05);
}

.type__heading_h1 {
font-size: 24px;
line-height: 29px;}

.type__heading_h2 {
font-size: 19px;
line-height: 23px;
}

.type__heading_h3 {
font-size: 12px;
line-height: 15px;
}

.type__heading_body {
font-size: 14px;
line-height: 24px;
}

.type__text_color_dark-shade100 {
color: #1F2041;
}

.type__text_color_dark-shade75 {
color: rgba(31, 32, 65, 0.75);
}

/* 950px */
@media only screen and (max-width: 950px) {
    .color-and-types__container { 
      display: flex;
      flex-direction: column;

    }
}
